"use client";

import { useRouter, usePathname } from "next/navigation"; // ÿßÿ≤ next/navigation
import { useTransition } from "react";

export default function LanguageSwitcher() {
  const router = useRouter();
  const pathname = usePathname();
  const [isPending, startTransition] = useTransition();

  const changeLocale = (locale: string) => {
    startTransition(() => {
      const segments = pathname.split("/");
      segments[1] = locale; // ŸÅÿ±ÿ∂ ÿ®ÿ± ÿß€åŸÜ ÿßÿ≥ÿ™ ⁄©Ÿá locale ÿØÿ± ÿßÿ®ÿ™ÿØÿß€å ŸÖÿ≥€åÿ± ÿßÿ≥ÿ™
      const newPathname = segments.join("/");
      router.replace(newPathname);
    });
  };

  return (
    <div className="flex gap-2">
      <select
        className="border rounded px-2 py-1"
        defaultValue="en" // ŸÖÿ´ŸÑÿß 'fa' €åÿß 'en'
        onChange={(e) => changeLocale(e.target.value)}
      >
        <option value="en">En</option>
        <option value="fa">ŸÅÿßÿ±ÿ≥€å</option>
        <option value="hy">’Ä’°’µ’•÷Ä’•’∂</option>
      </select>
    </div>
  );
}
